import {
	all,
	fork,
	takeLatest,
	takeEvery,
	call,
	put,
	take
} from "redux-saga/effects";
import {
	LOG_IN_REQUEST,
	LOG_IN_FAILURE,
	LOG_IN_SUCCESS
} from "../reducers/user";

const HELLO_SAGA = "HELLO_SAGA";

function loginAPI() {
	//서버에 요청을 보내는 부분
}

function* login() {
	try {
		yield call(loginAPI);
		yield put({
			type: LOG_IN_SUCCESS
		});
	} catch (e) {
		console.error(e);
		yield put({
			type: LOG_IN_FAILURE
		});
	}
}
function* watchLogin() {
	yield takeEvery(LOG_IN, login);
}

export default function* userSaga() {
	yield all([fork(watchLogin)]);
}
